import React from 'react';
import { useNavigate } from 'react-router-dom'

export const isAuthenticated = () => {
    const accessToken = sessionStorage.getItem('accessToken'); //    
  
    //       
    if (accessToken) {
      //       
      // :      
      return true; //     
    }
  
    return false;
  };


// 
export function getCodeList(codeNm) {
    var rtnCodeList = [];

    //  JSON  
    const jsonCodeData = sessionStorage.getItem('codeData');

    // JSON   JavaScript  
    const codeList = JSON.parse(jsonCodeData);

    if (codeList == null || !codeList) {
        return [];
    }

    // userData  
    for (const c of codeList) {
        //console.log(c);
        if (c.code == codeNm) {
            rtnCodeList = c.codes;
        }
    }

    if (rtnCodeList.length == 0) {
        console.log(' .');
    }

    return rtnCodeList;
}

export function throwError(error,navigate) {
    const statusCode = error.response.status;
    console.log('>>>error');
    console.log(error)
    

    //     
    switch (statusCode) {
    case 401:
        navigate('/login');
        break;
    case 404:
        navigate('/404');
        break;
    case 500:
        navigate('/500');
        break;
    case 503:
        navigate('/login');
        break;
    default:
        navigate('/error');
    }

}

/******************************************************************************
* Function Name: getCurrentTime
* Description  :   
* Return       : yyyyMMddHHmmSS
******************************************************************************/
export function getCurrentTime() {
    var current = new Date();

    var year = current.getFullYear();
    var month = current.getMonth() + 1;
    var day = current.getDate();
    var hour = current.getHours();
    var min = current.getMinutes();
    var sec = current.getSeconds();

    if (month < 10) {
        month = "0" + month;
    }
    if (day < 10) {
        day = "0" + day;
    }
    if (hour < 10) {
        hour = "0" + hour;
    }
    if (min < 10) {
        min = "0" + min;
    }
    if (sec < 10) {
        sec = "0" + sec;
    }

    return "" + year + month + day + hour + min + sec;
}

/******************************************************************************
* Function Name: getCurrentDate
* Description  :   
* Return       : yyyy-MM-dd
******************************************************************************/
export function getCurrentDate() {
    var current = new Date();

    console.log(current);

    var year = current.getFullYear();
    var month = current.getMonth() + 1;
    var day = current.getDate();

    if (month < 10) {
        month = "0" + month;
    }
    if (day < 10) {
        day = "0" + day;
    }
    return "" + year + '-' + month + '-' + day
}

/* ----------------------------------------------------------------------------
 *      (+-)  
 *
 *   -----
 * pInterval : "yyyy"   , "m"   , "d"   
 * pAddVal  :     ()
 * pYyyymmdd :    
 * pDelimiter : pYyyymmdd     ( "" )
 *
 *  ----
 * yyyymmdd       yyyy?mm?dd 
 *
 *  ---
 * 2008-01-01  3   ==> getAddDate("d", 3, "2008-08-01", "-");
 * 20080301  8   ==> getAddDate("m", 8, "20080301", "");
 --------------------------------------------------------------------------- */
export function getAddDate(pInterval, pAddVal, pYyyymmdd, pDelimiter) {
    var yyyy;
    var mm;
    var dd;
    var cDate;
    var cYear, cMonth, cDay;

    console.log(pInterval + "" + pAddVal + "" + pYyyymmdd + "" + pDelimiter);

    if (pDelimiter != "") {
        pYyyymmdd = pYyyymmdd.replace(eval("/\\" + pDelimiter + "/g"), "");
    }

    if (pYyyymmdd == "") {
        return;
    }

    yyyy = pYyyymmdd.substr(0, 4);
    mm = pYyyymmdd.substr(4, 2);
    dd = pYyyymmdd.substr(6, 2);

    if (pInterval == "yyyy") {
        yyyy = (yyyy * 1) + (pAddVal * 1);
    } else if (pInterval == "m") {
        mm = (mm * 1) + (pAddVal * 1);
    } else if (pInterval == "d") {
        dd = (dd * 1) + (pAddVal * 1);
    }

    cDate = new Date(yyyy, mm - 1, dd); // 12, 31       .
    cYear = cDate.getFullYear();
    cMonth = cDate.getMonth() + 1;
    cDay = cDate.getDate();

    cMonth = cMonth < 10 ? "0" + cMonth : cMonth;
    cDay = cDay < 10 ? "0" + cDay : cDay;

    if (pDelimiter != "") {
        console.log(cYear + pDelimiter + cMonth + pDelimiter + cDay);
        return cYear + pDelimiter + cMonth + pDelimiter + cDay;
    } else {
        console.log("" + cYear + cMonth + cDay);
        return "" + cYear + cMonth + cDay;
    }
}

